@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  padding: 0;
  border: none;
  font: inherit;
  cursor: pointer;
  color: #ffffff;
  background-color: #68B738;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
.button:hover {
  background-color: #5EAA2F;
}
.button:active .button__text {
  opacity: 0.3;
}
.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
