}
.page-body {
  font-family: Oswald, Arial, sans-serif;
  margin: 0;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
.main-header {
  box-sizing: border-box;
  padding: 0;
  padding: 11px 0 17px 0;
}
.main-header__link--bottom {
  padding-bottom: 220px;
}
.main-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-header__link {
  display: flex;
  align-items: center;
}
.main-header__link:hover {
  opacity: 0.8;
}
.main-header__link:active {
  opacity: 0.6;
}
.main-header__logo-text {
  margin-left: 57px;
}
@media (min-width: 768px) {
  .main-header {
    box-sizing: border-box;
    padding: 0;
    padding-top: 25px;
  }
  .main-header__link--bottom {
    padding-bottom: 0;
  }
  .main-header__logo-text {
    display: none;
  }
  .main-header__logo-image {
    width: 174px;
    height: 52px;
  }
  .main-header__container {
    display: grid;
    grid-template-columns: 174px auto;
    align-items: center;
    min-height: 51px;
    box-sizing: border-box;
  }
}
@media (min-width: 1440px) {
  .main-header {
    box-sizing: border-box;
    position: absolute;
    z-index: 100;
    left: 50%;
    padding: 0;
    padding-top: 55px;
    transform: translateX(-50%);
  }
}
@media (max-width: 767px) {
  .navigation__list-menu-open {
    list-style: none;
    padding: 0;
    margin: 0;
    position: absolute;
    left: 50%;
    top: 71px;
    width: 320px;
    transform: translateX(-50%);
    display: block;
    text-align: center;
    background-color: #ffffff;
    z-index: 101;
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
  }
}
.navigation__list-menu {
  display: none;
}
.navigation__button-menu {
  background: none;
  margin: 0;
  padding: 0;
  border: none;
}
@media (min-width: 768px) {
  .navigation__button-menu {
    display: none;
  }
}
.navigation__button::before {
  content: url("../img/icons/burger-open.svg");
  width: 24px;
  height: 19px;
}
.navigation__button--toggle {
  width: 24px;
  height: 19px;
  background: none;
  position: relative;
  cursor: pointer;
  top: -6px;
}
.navigation__button--toggle::before {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: #000000;
  left: 0;
  transform: rotate(45deg);
}
.navigation__button--toggle::after {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: #000000;
  left: 0;
  transform: rotate(-45deg);
}
.navigation__link {
  display: block;
  padding: 22px 0 24px 0;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  position: relative;
  border: 1px solid #eaeaea;
}
@media (min-width: 768px) {
  .navigation__button::before {
    display: none;
  }
  .navigation__button {
    display: none;
  }
  .navigation__item {
    margin-right: 36px;
  }
  .navigation__item:last-child {
    margin-right: 0;
  }
  .navigation__list-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
  }
  .navigation__list-menu-open {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
    font-size: 18px;
  }
  .navigation__link--active::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #68B738;
    position: absolute;
    bottom: -8px;
    left: 0;
  }
  .navigation__link--green:hover,
  .navigation__link--green-white:hover {
    opacity: 0.6;
  }
  .navigation__link--green:active,
  .navigation__link--green-white:active {
    opacity: 1;
  }
  .navigation__link--green:active::after,
  .navigation__link--green-white:active::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #68B738;
    position: absolute;
    bottom: -8px;
    left: 0;
  }
  .navigation__link {
    padding: 0;
    border: none;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .navigation__list-menu-open {
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .navigation__link--green-white {
    color: #ffffff;
  }
  .navigation__link--green-white:hover {
    opacity: 0.6;
  }
  .navigation__link--green-white:active {
    opacity: 1;
  }
  .navigation__link--green-white:active::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    position: absolute;
    bottom: -8px;
    left: 0;
  }
  .navigation__link--active-white::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    position: absolute;
    bottom: -8px;
    left: 0;
  }
  .navigation__list-menu {
    font-size: 20px;
  }
  .navigation__item {
    margin-right: 35px;
  }
}
